import React from 'react';
import { Route } from "react-router-dom";
import './App.css';
import * as Cmpt from './example';

function App() {
  return (
    <div className='App'>
      <Route exact path='/animate-transitions' component={Cmpt.AnimatedTransitions} />
      <Route exact path='/basic' component={Cmpt.Basic} />
      <Route exact path='/custom-link' component={Cmpt.CustomLink} />
      <Route exact path='/model-gallery' component={Cmpt.ModalGallery} />
      <Route exact path='/nesting' component={Cmpt.Nesting} />
      <Route exact path='/no-match-40' component={Cmpt.NoMatch404} />
      <Route exact path='/preventing-transitions' component={Cmpt.PreventingTransitions} />
      <Route exact path='/query-parameter' component={Cmpt.QueryParameters} />
      <Route exact path='/recursive-paths' component={Cmpt.RecursivePaths} />
      <Route exact path='/redirects-auth' component={Cmpt.RedirectsAuth} />
      <Route exact path='/route-config' component={Cmpt.RouteConfig} />
      <Route exact path='/sidebar' component={Cmpt.Sidebar} />
      <Route exact path='/static-router-context' component={Cmpt.StaticRouterContext} />
      <Route exact path='/url-parameter' component={Cmpt.UrlParameters} />
    </div>
  );
}

export default App;
